<!--我参与的活动页-->
<!--created by zhaoyizhu-->
<template>
  <div class="page-mine">
    <div class="list">
      <ul class="clearfix">
        <li v-for="item in list" :key="item.id" @click="goDetail(item.periodCode)">
          <div class="img-wrap">
            <img :src="$supImg(item.detailImgs[0], '214x160')" v-if="item.detailImgs">
          </div>
          <div class="info-wrap">
            <div class="p-name">{{item.goodsName}}</div>
            <div class="p-state clearfix">
              <div class="time">{{item.vo.createTime}}</div>
              <div class="state">
                <span class="state-prize" @click.stop="getPrize(item.periodCode, item.vo.joinUserHeadImg)" v-if="item.vo.prized && !item.vo.received">领取好物</span>
                <span class="state-on" v-if="item.periodStatus === 903 && !item.vo.prized">进行中</span>
                <span class="state-end" v-if="item.periodStatus === 904 && !item.vo.prized">已结束</span>
                <span class="state-get" v-if="item.vo.prized && item.vo.received">已领取好物</span>
              </div>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <div class="part-end" v-if="allLoad && list.length > 10"><span>已经到底啦</span></div>
    <div class="part-none" v-if="allLoad && list.length === 0">还没有参与活动哦～</div>
    <div class="part-loading" v-if="!allLoad"><i></i>加载中...</div>
    <userInfo ref="infoDialog" :periodCode="periodCode" :userImg="userImg" @callback="callback"/>
  </div>
</template>

<script>
import * as util from '@/assets/js/util'
import { mapState, mapActions } from 'vuex'
import userInfo from './user-info.vue'
export default {
  name: 'page-mine',
  components: { userInfo },
  data () {
    return {
      periodCode: '', // 商品编号
      userImg: ''
    }
  },
  created () {
    document.body.style.background = '#fff'
    document.body.scrollTop = 0
  },
  computed: {
    ...mapState('mine', [
      'allLoad',
      'list'
    ]),
    ...mapState('common', [
      'jdInfo' // 京东用户信息
    ])
  },
  async mounted () {
    if (await this.judgeJdIn()) {
      this.loadData()
      // 微信分享图文控制
      util.pageWxShare()
    }
  },
  methods: {
    ...mapActions('common', [
      'judgeJdIn' // 判断是否已登录
    ]),
    ...mapActions('mine', [
      'loadData' // 加载当前页面数据
    ]),
    // 领取好物
    getPrize (periodCode, userImg) {
      this.$refs.infoDialog.show = true
      this.periodCode = periodCode
      this.userImg = userImg
    },
    // 信息填写完成后 重载数据
    callback () {
      this.loadData()
    },
    // 点击跳往详情页
    goDetail (periodCode) {
      let encodeJdInfo = this.jdInfo.indexOf('%') === -1 ? encodeURIComponent(this.jdInfo) : this.jdInfo
      location.href = `${location.protocol}//${location.host}/c2c/star/share?activityInfoId=${periodCode}&joinPin=${encodeJdInfo}`
      // this.$router.push(`/share?activityInfoId=${periodCode}&joinPin=${this.jdInfo}`)
    }
  }
}
</script>

<style type="text/scss" rel="stylesheet/scss" lang="scss">
.page-mine {
  background: #fff;
  .list {
    ul li {
      width: 750px;
      height: 206px;
      padding: 23px;
      border-bottom: 1PX solid #F1F1F1;
      .img-wrap {
        float: left;
        margin-right: 24px;
        background: #efefef;
        width: 216px;
        height: 160px;
        img {
          width: 100%;
          height: 100%;
          overflow: hidden;
        }
      }
      .info-wrap {
        .p-name {
          font-size: 28px;
          font-weight: 300;
          color: #333333;
          height: 84px;
          line-height: 42px;
          margin-bottom: 28px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
        }
        .p-state {
          height: 48px;
          line-height: 48px;
          .time {
            float: left;
            font-size: 24px;
            font-weight: 300;
            color: #999999;
          }
          .state {
            float: right;
            &-prize {
              position: relative;
              top: -2px;
              display: inline-block;
              font-size: 28px;
              padding: 5px 28px;
              text-align: center;
              background: #FF3434;
              border-radius: 92px;
              color: #FFFFFF;
              font-weight: 300;
            }
            &-on {
              color: #FF3434;
            }
            &-end, &-get, &-nostart {
              color: #CCCCCC;
            }
            &-get {
              color: #FF3434;
            }
          }
        }
      }
    }
  }
  .part-end {
    text-align: center;
    padding: 24px 0 40px;
    font-size: 24px;
    color: #ccc;
    span {
      position: relative;
      &:before, &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: -65px;
        height: 1PX;
        width: 50px;
        background: linear-gradient(to right, transparent 0%, #ccc 100%);
      }
      &:after {
        left: auto;
        right: -65px;
        background: linear-gradient(to left, transparent 0%, #ccc 100%);
      }
    }
  }
  .part-none {
    color: #333;
    font-size: 24px;
    text-align: center;
    padding: 20px 0;
  }
  .part-loading {
    text-align: center;
    padding: 20px;
    font-size: 24px;
    color: #ccc;
    i {
      display: inline-block;
      vertical-align: -5px;
      background: url("../../assets/img/loading.gif") no-repeat center;
      background-size: cover;
      width: 25px;
      height:25px;
      margin-right: 5px;
    }
  }
}
/*安卓下一些特殊处理*/
.underAndroid {
  .page-mine {
    .state-prize {
      padding-top: 7px !important;
    }
  }
}
</style>



