<!--弹出的信息填写页-->
<!--created by zhaoyizhu-->
<template>
  <div class="page-user-info">
    <transition name="info-mask">
      <div class="dialog-mask" v-if="show">
        <div class="info-dialog">
          <div class="info-dialog-con">
            <div class="user-img">
              <img v-lazy="{src: $supImg(userImg, '88x88'), error: $reqImg('userDefaultGrey.png'), loading: $reqImg('userDefaultGrey.png')}"/>
            </div>
            <div class="tip">恭喜您获得明星好物</div>
            <div class="info-input">
              <div class="prompt">请填写手机号及收货地址，我们会在10个工作日内给您发货</div>
              <input type="text" name="name" placeholder="请输入姓名" v-model="name"/>
              <input type="text" name="phone" placeholder="请输入手机号" v-model="phone"/>
              <div class="select-address" @click="selectAddress">
                <input type="text" name="address" placeholder="请选择地区" readonly :value="address" ref="addressInput"/>
                <div class="more clearfix"><span>&#183;</span><span>&#183;</span><span>&#183;</span></div>
              </div>
              <input type="text" name="detail" placeholder="请输入详细地址" v-model="detail"/>
            </div>
            <div class="submitBtn">
              <span class="enableBtn" @click="submit" v-if="name && phone && address && detail">提交</span>
              <span v-else>提交</span>
            </div>
          </div>
          <div class="info-dialog-close"><i @click="closeDialog"></i></div>
        </div>
      </div>
    </transition>
    <!-- 选择地址 -->
    <jd-address ref="addressRef" @address="addressFuc"/>
  </div>
</template>
<script>
import * as util from '@/assets/js/util'
import { mapState, mapActions } from 'vuex'
export default {
  name: 'page-user-info',
  props: {
    periodCode: String,
    userImg: String
  },
  data () {
    return {
      show: false,
      address: '',
      name: '',
      phone: '',
      addressInfo: {}, // 存放选择的地址（名字和id）
      detail: ''
    }
  },
  computed: {
    ...mapState('mine', [
      'showD'
    ])
  },
  methods: {
    ...mapActions('mine', [
      'submitForm'
    ]),
    // 点击选择地址
    selectAddress () {
      this.$refs.addressRef.show = true
      this.$refs.addressInput.blur()
    },
    // 选中的地址
    addressFuc (val) {
      this.address = val.province + val.city + val.county + val.town
      this.addressInfo = val
    },
    // 提交
    async submit () {
      let param = {
        periodCode: this.periodCode || undefined,
        receiveName: this.name,
        receiveMobile: this.phone,
        receiveProvinceId: this.addressInfo.provinceId || undefined,
        receiveCityId: this.addressInfo.cityId || undefined,
        receiveDistrictId: this.addressInfo.countyId || undefined,
        receiveTownId: this.addressInfo.townId || undefined,
        receiveAddress: this.detail
      }
      await this.submitForm(param)
      this.show = this.showD
      if (!this.show) {
        this.clearForm()
        this.$emit('callback')
      }
    },
    // 关闭填写信息弹框
    closeDialog () {
      this.show = false
      this.clearForm()
      util.forbidScroll(false)
    },
    // 清空表单
    clearForm () {
      this.name = ''
      this.phone = ''
      this.address = []
      this.detail = ''
    }
  }
}
</script>
<style type="text/scss" rel="stylesheet/scss" lang="scss">
.page-user-info {
  .dialog-mask {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .5);
    position: fixed;
    left: 0;
    top: 0;
    z-index: 9;
    .info-dialog {
      position: absolute;
      width: 580px;
      top: 50%;
      left: 50%;
      -moz-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      -webkit-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      &-con {
        width: 100%;
        background: #FFFFFF;
        border-radius: 12px;
        overflow: hidden;
        margin-bottom: 48px;
        padding-bottom: 50px;
        .user-img {
          text-align: center;
          margin: 56px 0 22px 0;
          img {
            width: 88px;
            height: 88px;
            border-radius: 50px;
          }
        }
        .tip {
          font-size: 34px;
          color: #030303;
          text-align: center;
          font-weight: bold;
          margin-bottom: 46px;
        }
        .info-input {
          padding: 0 48px;
          input {
            width: 484px;
            height: 72px;
            margin-top: 16px;
            background: #F7F7F7;
            border-radius: 4px;
            font-size: 28px;
            padding: 0 16px;
            color: #333;
            &::-webkit-input-placeholder{
              color: #CCCCCC;
              font-weight: 300;
            }
            &::-moz-placeholder{   /* Mozilla Firefox 19+ */
              color: #CCCCCC;
              font-weight: 300;
            }
            &:-moz-placeholder{    /* Mozilla Firefox 4 to 18 */
              color: #CCCCCC;
              font-weight: 300;
            }
            &:-ms-input-placeholder{  /* Internet Explorer 10-11 */
              color: #CCCCCC;
              font-weight: 300;
            }
          }
          .prompt {
            font-size: 24px;
            font-weight: 300;
            color: #999999;
          }
          .select-address {
            position: relative;
            input {
              padding-right: 76px;
            }
            .more {
              position: absolute;
              width: 100px;
              right: 26px;
              top: 22px;
              color: #999;
              text-align: right;
              span {
                display: inline-block;
                font-size: 50px;
                width: 30px;
                margin-right: -18px;
              }
            }
            /*i {
              background: url(../../assets/img/more@3x.png) no-repeat;
              background-size: 100%;
              display: inline-block;
              width: 24px;
              height: 6px;
              position: absolute;
              right: 16px;
              top: 50px;
              color: #999;
            }*/
          }
        }
        .submitBtn {
          position: relative;
          text-align: center;
          margin-top: 48px;
          z-index: 1;
          span {
            position: relative;
            display: inline-block;
            width: 350px;
            padding: 18px 0;
            background: #E9E9E9;
            border-radius: 100px;
            font-size: 28px;
            color: #CCCCCC;
            &:after {
              content: '';
              position: absolute;
              top: 40%;
              left: 5%;
              width: 90%;
              height: 50%;
              z-index: -1;
              box-shadow: 0 10px 30px rgba(230, 230, 230, .8);
            }
            &.enableBtn {
              background: #FF3434;
              color: #FFFFFF;
              &:after {
                box-shadow: 0 10px 30px rgba(255, 0, 0, .4);
              }
            }
          }
        }
      }
      &-close {
        text-align: center;
        i {
          background: url(../../assets/img/close@3x.png) no-repeat;
          background-size: 100%;
          display: inline-block;
          width: 64px;
          height: 64px;
        }
      }
    }
  }
  .info-mask-enter-active, .info-mask-leave-active {
    transition: all ease .3s;
  }
  .info-mask-enter, .info-mask-leave-to {
    opacity: 0;
  }
}
</style>



